/* Basic reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  background: #f0f2f5;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

/* Login Page */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-box {
  background: #fff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  width: 320px;
  gap: 15px;
}

.login-box h2 {
  text-align: center;
  color: #333;
}

.login-box input {
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  transition: all 0.2s ease;
}

.login-box input:focus {
  border-color: #4cafef;
  box-shadow: 0 0 8px #4cafef77;
}

.login-box button {
  padding: 12px;
  background-color: #4cafef;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s;
}

.login-box button:hover {
  background-color: #0077cc;
}

.error-popup {
  background: #ff4d4d;
  padding: 8px;
  border-radius: 6px;
  color: white;
  text-align: center;
}

/* Portal Page */
.portal-container {
  width: 95%;
  max-width: 1200px;
  background: #fff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-section,
.food-section {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.form-section input,
.food-section input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  flex: 1;
}

.form-section button,
.food-section button {
  padding: 10px 15px;
  background-color: #4cafef;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.table-section {
  margin-top: 20px;
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th,
td {
  padding: 10px;
  text-align: center;
  border-bottom: 1px solid #ccc;
}

th {
  background-color: #4cafef;
  color: white;
}
