.portal-container {
  max-width: 900px;
  margin: auto;
  padding: 20px;
  background: #f8f9fd;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  font-family: Arial, sans-serif;
}

h1, h2 {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 20px;
}

.form-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin: 15px 0;
}

input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  transition: 0.3s;
  min-width: 150px;
}

input:focus {
  border-color: #3498db;
}

button {
  background-color: #3498db;
  border: none;
  padding: 10px 15px;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  background-color: #2980b9;
}

.records-section {
  margin-top: 30px;
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
  min-width: 700px;
}

th, td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

th {
  background-color: #3498db;
  color: white;
}

td button {
  margin: 2px;
  padding: 5px 10px;
  font-size: 0.9rem;
}

button[style] {
  background-color: #e74c3c !important;
}

.toast {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 12px 20px;
  border-radius: 8px;
  color: white;
  font-weight: bold;
  z-index: 9999;
  opacity: 0.9;
  transition: all 0.3s ease-in-out;
}

.toast.success {
  background-color: #2ecc71;
}

.toast.error {
  background-color: #e74c3c;
}

@media (max-width: 768px) {
  .form-section {
    flex-direction: column;
    align-items: center;
  }

  input {
    width: 80%;
  }

  table {
    font-size: 0.9rem;
  }
}
